<template>
  <div class="container zindex-10">
    <div
      class="filter-row flexbox align-items-center justify-between p-1 mobile-flex-wrap mobile-p-t-2"
    >
      <div
        class="title-wrapper flexbox align-items-center justify-between pos-relative mobile-w-100 no-line"
      >
        <div class="flexbox align-items-center">
          <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" class="mr-r-1" />
          <span class="title">Return Buses</span>
        </div>
        <a
          href="#"
          @click.prevent="toggleReturnFilterForm"
          class="hidden mobile-display-inline-block"
        >
          <img src="@/assets/icons/caret-down.svg" alt="Passaner Icon" style="width: 20px;" />
        </a>
      </div>

      <div
        class="flex-1 flexbox align-items-center mobile-flex-wrap mobile-p-t-1 mobile-p-b-1"
        v-if="showReturnFilterForm"
      >
        <form
          @submit.prevent="getSubRequest({town_from: trip.town_from_return, town_to: trip.town_to_return, travelling_on: trip.returning_on, is_return: true})"
          class="inline-form w-100 flexbox align-items-center justify-end mobile-flex-wrap"
        >
          <div class="form-group w-20 mr-r-1 mobile-mr-r-0 mobile-w-100">
            <label class id="filter-travelling-form">Returning From</label>
            <v-select
              v-model="trip.town_from_return"
              class="modern-selector is-inline"
              placeholder="Returning From"
              label="town_name"
              id="filter-travelling-form"
              :options="all_towns"
            ></v-select>
          </div>

          <div class="form-group w-20 mr-r-1 mobile-mr-r-0 mobile-w-100">
            <label class id="filter-travelling-to">Returning To</label>
            <v-select
              v-model="trip.town_to_return"
              class="modern-selector is-inline"
              placeholder="Travelling To"
              label="town_name"
              id="filter-travelling-to"
              :options="all_towns"
            ></v-select>
          </div>

          <div class="form-group w-20 mr-r-1 mobile-mr-r-0 mobile-w-100">
            <label id="filter-travelling-date">Returning On</label>
            <DatePicker
              v-model="trip.returning_on"
              :min-date="new Date()"
              :max-date="maxTravelDate"
              :input-props="{
                id: 'filter-travelling-date',
                class: 'vc-calendar-custom is-inline',
                placeholder: 'Travelling On',
                readonly: true
                }"
            />
          </div>

          <!-- Submit Form -->
          <div class="form-group w-20 mobile-w-100">
            <label for="filter-btn" class="m-invisible mobile-hidden">Filter</label>
            <button class="btn" id="filter-btn">FILTER</button>
          </div>
          <!-- /Submit Form -->
        </form>
      </div>
    </div>
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

/* Max date, 4 days from today */
let getMaxTravelDate = () => {
  let today = new Date();
  let tomorrow = new Date(today);
  return tomorrow.setDate(tomorrow.getDate() + 4);
};

export default {
  name: "AppReturnTripFilter",
  data() {
    return {
      travelDate: null,
      showReturnFilterForm: true,
      maxTravelDate: getMaxTravelDate(),
      formData: {
        to: "",
        from: ""
      }
    };
  },
  methods: {
    ...mapActions(["fetchTowns", "getSubRequest"]),
    toggleReturnFilterForm() {
      this.showReturnFilterForm = !this.showReturnFilterForm;
    },
    isOkay() {
      return true;
    }
  },
  computed: mapGetters(["all_towns", "trip"]),
  created() {
    /*Get all towns through the API*/
    this.fetchTowns();
  },
  mounted() {
    let self = this;

    //scroll to top
    window.scrollTo(0, 0);
    //check if mobile
    let isMobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
      navigator.userAgent
    );
    if (isMobile) {
      self.showReturnFilterForm = false;
    }
  }
};
</script>